<template>
    <nav class="nav container">

        <a href="" class="logo">
            <img src="@/assets/images/logo.svg" alt="">
            <h1>vue weather</h1>
        </a>

        <div class="nav__right">

            <button @click="switchTheme">
                <img src="@/assets/images/drop.svg" alt="">
            </button>

            <input type="text" placeholder="Введите город" v-model="city" @keydown.enter="getWeather(city), city = ''">
        </div>

    </nav>
</template>

<script>

import { mapActions } from 'vuex'

export default {
    data() {
        return {
            city: ''
        }
    },
    methods: {
        // getWeather() {
        //     this.$store.dispatch('getWeather')
        // }
        ...mapActions(['getWeather']),

        switchTheme() {

            let value = document.body.style.getPropertyValue('--bg')

            if (value == 'white') {
                document.body.style.setProperty('--bg', '#292828')
            } else {
                document.body.style.setProperty('--bg', 'white')
            }

        }


    },
    created() {
        document.body.style.setProperty('--bg', 'white')
    }
}
</script>

